html {
  box-sizing: border-box; }

*,
*::before,
*::after {
  box-sizing: inherit; }

body {
  margin: 0 auto;
  padding: 0;
  min-width: 320px;
  font-family: "Roboto", "Arial", sans-serif;
  font-size: 15px;
  line-height: 20px;
  font-weight: 400;
  font-style: normal;
  background-color: #ffffff;
  color: #000000; }

a {
  text-decoration: none; }

img {
  max-width: 100%;
  height: auto; }

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

@font-face {
  font-family: "Roboto";
  font-weight: 400;
  font-style: normal;
  src: url("../fonts/roboto/Roboto.woff2") format("woff2"), url("../fonts/roboto/Roboto.woff") format("woff");
  font-display: swap; }

@font-face {
  font-family: "Roboto";
  font-weight: 500;
  font-style: normal;
  src: url("../fonts/roboto/Robotomedium.woff2") format("woff2"), url("../fonts/roboto/Robotomedium.woff") format("woff");
  font-display: swap; }

@font-face {
  font-family: "Roboto";
  font-weight: 700;
  font-style: normal;
  src: url("../fonts/roboto/Robotobold.woff2") format("woff2"), url("../fonts/roboto/Robotobold.woff") format("woff");
  font-display: swap; }

@font-face {
  font-family: "Roboto";
  font-weight: 900;
  font-style: normal;
  src: url("../fonts/roboto/Robotoblack.woff2") format("woff2"), url("../fonts/roboto/Robotoblack.woff") format("woff");
  font-display: swap; }

@font-face {
  font-family: "Raleway";
  font-weight: 900;
  font-style: normal;
  src: url("../fonts/raleway/ralewayblack.woff2") format("woff2"), url("../fonts/raleway/ralewayblack.woff") format("woff");
  font-display: swap; }

@font-face {
  font-family: "Circle";
  font-weight: 400;
  font-style: normal;
  src: url("../fonts/circle/circle.woff2") format("woff2"), url("../fonts/circle/circle.woff") format("woff");
  font-display: swap; }

.container {
  width: 1200px;
  margin: 0 auto;
  padding: 0 1.25%; }

@media (min-width: 768px) and (max-width: 1200px) {
  .container {
    width: 768px;
    padding: 0 2.9%; } }

@media (min-width: 320px) and (max-width: 768px) {
  .container {
    width: 320px;
    padding: 0 6.4%; } }

.button {
  display: block;
  box-sizing: border-box;
  text-align: center;
  vertical-align: middle; }
  .button--active {
    background-color: #00ffd7; }
    .button--active:hover {
      box-shadow: 0 2px 5px rgba(0, 255, 215, 0.6); }

.header {
  background-color: #fbfbfb; }

.header__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 20px;
  padding-bottom: 20px; }

.header__left {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 45.6%; }

.header__logo-image {
  width: 141px;
  height: 60px; }

.header__toggle {
  display: none; }

.header__right {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  width: 31.9%; }

.header__phone {
  width: 46%;
  font-size: 18px;
  font-weight: 700;
  color: #000000; }

.header__button {
  margin-top: -7px;
  padding-top: 12px;
  padding-bottom: 13px;
  width: 52%;
  height: 45px;
  font-size: 16px;
  font-weight: 700;
  color: #000000; }

@media (min-width: 320px) and (max-width: 1200px) {
  .header__wrapper {
    position: relative;
    padding: 8px 8%; }
  .header__left {
    width: 23.4%; }
  .header__logo-image {
    width: 86px;
    height: 37px; }
  .header__right {
    white-space: nowrap;
    width: 19.4%; }
  .header__toggle {
    display: block;
    position: relative;
    width: 26px;
    height: 31px;
    background-color: #fbfbfb;
    cursor: pointer;
    border: none; }
    .header__toggle::before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 26px;
      height: 2px;
      box-shadow: 0 3px 0 0 rgba(56, 56, 56, 0.3), 0 13px 0 0 rgba(56, 56, 56, 0.3), 0 22px 0 0 rgba(56, 56, 56, 0.3); }
  .header__phone {
    width: 100%;
    font-size: 14px; }
  .header__button {
    display: none; } }

@media (min-width: 320px) and (max-width: 768px) {
  .header__wrapper {
    padding: 7px 20px; }
  .header__left {
    width: 100%; }
  .header__right {
    display: none; }
  .header__toggle {
    width: 18px;
    height: 25px; }
    .header__toggle::before {
      width: 18px;
      box-shadow: 0 2px 0 0 rgba(56, 56, 56, 0.3), 0 8px 0 0 rgba(56, 56, 56, 0.3), 0 14px 0 0 rgba(56, 56, 56, 0.3); } }

.nav {
  width: 62.6%; }

@media (min-width: 320px) and (max-width: 1200px) {
  .nav {
    position: absolute;
    display: none;
    top: 50px;
    left: 0;
    width: 207px; }
    .nav--show {
      display: block; } }

@media (min-width: 320px) and (max-width: 768px) {
  .nav {
    position: absolute;
    top: 49px;
    width: 100%; } }

.nav-list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  width: 335px;
  font-size: 10px;
  font-weight: 500;
  text-transform: uppercase; }

.nav-list__item {
  padding: 19px 19px 25px 0; }

.nav-list__item a {
  font: inherit;
  color: #707070; }
  .nav-list__item a:hover, .nav-list__item afocus {
    color: #000000; }

@media (min-width: 320px) and (max-width: 1200px) {
  .nav-list {
    flex-direction: column;
    width: 207px;
    font-family: "Circle", "Arial", sans-serif;
    font-size: 12px;
    line-height: 19px;
    font-weight: 400;
    background-color: #e7e7e7; }
  .nav-list__item {
    padding-top: 15px;
    padding-bottom: 6px;
    padding-left: 17px;
    text-align: center;
    box-shadow: 0 1px 2px rgba(0, 0, 0, 0.05); }
    .nav-list__item--hidden {
      display: none; }
  .nav-list__item a {
    color: #383838; } }

@media (min-width: 320px) and (max-width: 768px) {
  .nav-list {
    width: 100%; } }
